.lds-ring {
  display: inline-block;
  position: relative;
  margin-top: 26px;
  width: 80px;
  height: 80px;

  div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid #7AD392;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #7AD392 transparent transparent transparent;

    &:nth-child(1) {
      animation-delay: -0.45s; }

    &:nth-child(2) {
      animation-delay: -0.3s; }

    &:nth-child(3) {
      animation-delay: -0.15s; } } }

@keyframes lds-ring {
  0% {
    transform: rotate(0deg); }

  100% {
    transform: rotate(360deg); } }


.spiner-container {
  width: 140px;
  height: 140px;
  background: linear-gradient(90deg, #FFFFFF 0%, #FFFFFF 48.84%);
  box-shadow: 0px 4px 24px rgba(53, 63, 71, 0.08);
  border-radius: 16px;
  margin: auto; }
