.bg1 {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 10;
  display: block;
  background-size: contain;
  @include media-breakpoint-up('xl') {
    @include rfs(436px,'width');
    @include rfs(349px,'height');
    background: url('/img/bg/bg1.svg') right top no-repeat; }

  @include media-breakpoint-between('md','lg') {
    @include rfs(229px,'width');
    @include rfs(265px,'height');
    background: url('/img/bg/bg1_tablet.svg') right top no-repeat; }
  @include media-breakpoint-down('md') {
    @include rfs(115px,'width');
    @include rfs(229px,'height');
    background: url('/img/bg/bg1_mobile.png') right top no-repeat;
    background-size: contain;
    top: unset; } }
